$image-path: '/assets/img/';

/*
----------------------------------------
Typography
----------------------------------------
*/

$font-system: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial,
  sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji';
$font-sans: Inter, $font-system;
$font-mono: SFMono-Regular, Consolas, 'Liberation Mono', Menlo, monospace;

@font-face {
  font-family: Inter;
  src: url('https://static.figma.com/webfont/1/Inter-Regular.woff2?v=3.10')
      format('woff2'),
    url('https://static.figma.com/webfont/1/Inter-Regular.woff?v=3.10')
      format('woff');
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: Inter;
  src: url('https://static.figma.com/webfont/1/Inter-Medium.woff2?v=3.10')
      format('woff2'),
    url('https://static.figma.com/webfont/1/Inter-Medium.woff?v=3.10')
      format('woff');
  font-style: normal;
  font-weight: 500;
}

@font-face {
  font-family: Inter;
  src: url('https://static.figma.com/webfont/1/Inter-SemiBold.woff2?v=3.10')
      format('woff2'),
    url('https://static.figma.com/webfont/1/Inter-SemiBold.woff?v=3.10')
      format('woff');
  font-style: normal;
  font-weight: 600;
}

.usa-prose {
  font-weight: 400;
  a {
    @include typeset-link;
  }
}

.usa-intro {
  font-weight: $theme-font-weight-light;
}

.usa-logo__text {
  font-size: 40px;
}

.usa-logo {
  margin-left: 0;
}

.usa-header-container,
.usa-hero {
  background-color: #162e51;
}

.usa-header + .usa-hero {
  border-top: none;
  padding-top: 0;
}

.usa-header--extended .usa-nav {
  border-top: none;
}

.usa-nav {
  margin-bottom: -2em;
}

.usa-navbar {
  padding-top: 5em;
  padding-left: 0 !important;
  padding-right: 0 !important;
  border-bottom: none !important;
}

.usa-nav__secondary {
  margin-bottom: 9em;
  bottom: 2rem;
}

.usa-hero {
  padding-top: 40px;
  background-image: none;
  padding-bottom: 3rem;
  @include at-media('desktop') {
    padding-bottom: 2rem;
  }
}

.usa-hero__callout {
  background-color: none;
  max-width: none;
  padding: 0;
  h1,
  h2 {
    font-family: $font-sans;
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 130%;
    color: #97d4ea;
    max-width: 400px;
    padding: 0;
    margin: 0;

    // @include at-media('desktop') {
    //   padding-bottom: 40px;
    // }
    // width: 90%;
    // margin-top: 5px;
  }
  h1 {
    color: #97d4e9;
    margin-top: 3em;
    margin-bottom: 1em;

    @include at-media('tablet') {
      margin-left: -2em;
    }
  }
  @include at-media('desktop') {
    margin-top: 0;
  }
}

.usa-button.wide-button {
  width: 150px;
  font-weight: normal;
  margin-right: 0;
  margin-bottom: 0.5em;
  @include at-media('desktop') {
    width: 218px;
  }
}

.usa-navbar {
  @include at-media(mobile) {
    padding-top: 0;
    #header-logo {
      margin-top: 160px;
    }
  }
}

.usa-hero-placeholder-container {
  @include at-media('desktop') {
  }
  @include at-media('tablet') {
    display: block;
    // margin-top: -10em;
  }
  .hero-img-col {
    /*display: none;*/
    @include at-media('tablet-lg') {
      display: block;
      // margin-top: -10em;
    }
  }
}

.usa-hero-placeholder {
  max-width: 540px;
  width: 100%;
  height: auto;
  z-index: 10;
  // margin-top: 12em;
  // margin-left: 1em;
  // margin-top: 1em;
  /*margin: 0 0 1em 1em;*/
  padding-top: 2em;
  @include at-media('tablet') {
    margin: 1em 0 1em 1em;
    padding-top: 1em;
  }
}

.container-blue {
  background: #e1f3f8;
  @include at-media('desktop') {
    padding-bottom: 160px;
    background: #e1f3f8 url('/assets/img/waves.svg') top center no-repeat;
    /*uncomment for githubpages*/
    /*background-image: url('/assets/img/waves.svg'), url('/SimpleReport_Public_Site/assets/img/waves.svg');*/
    max-height: 390px;
  }
}

.usa-hero-subsection {
  font-family: $font-sans;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #1b1b1b;
  padding-top: 1em;
  // border: 1px solid #f00;
  padding-bottom: 1rem;
  @include at-media('tablet') {
    padding-bottom: 0;
  }
  @include at-media('desktop') {
    padding-top: 7em;
    // padding-bottom: 15em;
    // max-height: 180px;
  }
  p {
    max-width: 380px;
    color: #162e51;
  }
  @include at-media('desktop') {
    & > p {
      padding-top: 1em;
    }
  }
  ul {
    list-style: none;
    padding: 0;
    li {
      &:before {
        content: '';
        display: inline-block;
        width: 16px;
        font-family: $font-system;
      }

      font-weight: 400;
    }
  }
}

