.usa-section {
  img {
    margin-bottom: 24px;
  }

  .usa-bullets {
    p {
      /*margin: 0 auto 2em auto;*/
      font-family: $font-sans;
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      @include at-media('tablet') {
        margin-bottom: 0;
        font-size: 14px;
      }
      strong {
        font-weight: 600;
      }
    }
  }
  .usa-list {
    li {
      font-family: $font-system;
      font-weight: 400;
    }
  }

  &.blue-section {
    ul {
      padding-bottom: 40px;
    }
  }
}

.usa-section-list {
  background: white;

  .section-title-line {
    border-bottom: 1px solid #73b3e7;
    border-top: 1px solid #73b3e7;
    height: 94px;
    margin-right: -40px;
    width: 40px;

    + h2 {
      margin-top: 29px;
    }
  }

  .section-title-line.taller-section-title-line {
    height: 105px;

    + .huge-header {
      margin-top: 27px;
    }
  }

  .section-title + .section-columns {
    margin-top: 2em;
    /*margin-bottom: 1em;*/
  }

  h2 {
    font-family: $font-sans;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 151.28%;
    color: #162e51;
  }

  h3 {
    font-family: $font-mono;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;
    color: #1b1b1b;
    margin-bottom: 2em;
  }
  h4 {
    font-family: $font-sans;
    font-weight: 700;
    font-size: 14px;
  }

  .usa-prose {
    font-family: $font-sans;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 151.28%;
    color: #162e51;
  }

  .prose-f4 {
    font-family: $font-sans;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 151.28%;
    color: #3d4551;
  }

  .number-header {
    font-family: $font-sans;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 151.28%;
    color: #1b1b1b;
    padding-bottom: 1em;
    padding-top: 2em;
  }

  .usa-list {
    font-family: $font-sans;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 150%;
    color: #1b1b1b;
    padding-left: 1em;
    padding-top: 1em;

    li {
      padding-left: 5px;
    }
  }

  .grid-row.grid-gap {
    padding-bottom: 3em;
  }
}

.grid-gap-small-btm {
  padding-bottom: 2rem !important;
}

.cta-btn {
  color: #ffffff !important;
  text-decoration: none !important;
}

.grid-row {
  @include at-media('tablet') {
    padding: 0 5em;
  }
}

.page-docs {
  .grid-row {
    @include at-media('tablet') {
      padding: 0;
    }
  }

  .usa-layout-docs__main {
    h1,
    h2,
    h3 {
      color: color('primary-darker');
    }

    .usa-button {
      color: color('white');
      text-decoration: none;
    }
  }
}

.illustration-image {
  margin-bottom: 3em !important;
}

.usa-bullets {
  font-family: $font-sans;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 150%;
  color: #3d4551;
}

.usa-prose .usa-bullets {
  width: 100%;
  @include at-media('tablet') {
    max-width: 480px;
  }
}

.blue-section {
  background: #e1f3f8;
  color: white;
  padding-top: 2em !important;
  margin-bottom: -5em;
  @include at-media('tablet') {
    margin-bottom: -9em;
  }
  @include at-media('tablet-lg') {
    margin-bottom: -10em;
  }
  @media screen and (min-width: 1300px) {
    margin-bottom: -12em;
  }

  /*margin: 0 -10em;*/

  h2 {
    color: white;
    width: 217px;
  }

  .section-title-line {
    height: 128px;
  }

  h4,
  h3 {
    color: #ffbc78;
  }

  .usa-prose {
    font-family: $font-sans;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: #ffffff;
  }

  .usa-list {
    color: white;
    font-size: 14px;
    padding-left: 1em;
    padding-top: 1em;

    li {
      padding-left: 5px;
    }
  }

  .medium-header {
    font-family: $font-sans;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 150%;
    color: #3d4551;
    margin-bottom: 0.5em;
  }

  .prose {
    font-family: $font-sans;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    color: #3d4551;
  }
}

.divider-line {
  border-bottom: 1px solid #73b3e7;
  margin-top: 3em;
  width: 800px;
}

.usa-footer {
  .usa-footer__secondary-section {
    margin-top: 40px;
    background-color: #162e51;
    color: white;
    padding-bottom: 5em;

    .usa-footer__logo {
      margin-bottom: 3em;
    }

    .usa-footer__logo-img {
      max-width: 74px;
    }

    h3.usa-footer__logo-heading {
      font-family: $font-sans;
      font-style: normal;
      font-weight: normal;
      font-size: 20px;
      line-height: 151.28%;
    }

    a {
      color: white !important;
      font-family: $font-sans;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 150%;
    }
  }
  .tos-right {
    @include at-media('tablet') {
      text-align: right;
    }
  }
}

.text-align-right {
  text-align: right;
}

#header-logo {
  margin-top: 3em;
}

.large-button {
  padding: 12px 20px;
  width: 298px;
  height: 48px;
  background: #005ea2;
  border-radius: 4px;
  color: white !important;
  font-family: $font-sans;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  text-align: center;
}

.mobile-display {
  display: block;
  @include at-media('tablet') {
    display: none !important;
  }
}

.tablet-display {
  display: none;
  @include at-media('tablet') {
    display: block;
  }
}

.no-top-margin {
  margin-top: 0 !important;
}

.no-underline {
  text-decoration: none !important;
}

.home-link {
  svg {
    height: 1em;
    margin-bottom: -2px;
    margin-right: 0.5rem;
  }
}

.float-right {
  float: right;
}

.request-account-link {
  color: white;
  display: block;
  text-align: center;
}

.huge-header {
  font-family: $font-sans;
  font-style: normal;
  font-weight: 500;
  font-size: 40px;
  line-height: 130%;
  color: #162e51;
}

.medium-header {
  font-family: $font-sans;
  font-style: normal;
  font-weight: 500;
  font-size: 32px;
  line-height: 130%;
  color: #162e51;

  + .prose {
    padding-top: 24px;
  }
}

.small-header {
  font-family: $font-sans;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 151.28%;
  color: #000000;

  + .small-prose {
    padding-top: 24px;
  }
}

.prose {
  font-family: $font-sans;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 150%;
  color: #3d4551;
}

.small-prose {
  font-family: $font-sans;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  color: #1b1b1b;
}

.gap-row {
  height: 217px;
}

.gap-row-2 {
  height: 200px;
}

.gap-row-3 {
  height: 122px;
}

.gap-row-4 {
  height: 82px;
}

.brown-bubble {
  position: absolute;
  top: -160px;
  left: -30px;

  @include at-media('tablet-lg') {
    top: -175px;
    left: 8px;
  }
}

.animation-1 {
  position: relative;

  @include at-media('tablet-lg') {
    top: -40px;
  }
}

.blue-bubble {
  position: absolute;
  top: -120px;

  @include at-media('tablet-lg') {
    top: 10px;
    left: 65px;
  }
}

.animation-2 {
  position: relative;
  top: -75px;

  @include at-media('tablet-lg') {
    top: -17px;
    left: -50px;
  }
}

.mid-wave-top,
.mid-wave-bottom {
  width: 100%;
}

.mid-wave-top {
  position: relative;
  top: -57px;
  @include at-media('tablet') {
    top: -80px;
  }
  @include at-media('tablet-lg') {
    top: -94px;
  }
  @media screen and (min-width: 1200px) {
    top: -120px;
  }
  @media screen and (min-width: 1600px) {
    top: -150px;
  }
}

.mid-wave-bottom {
  margin-top: 2em;
}

.cdc-logo-large {
  margin-left: 1em;
  margin-top: 0.5em;
}

.shield {
  margin-left: 3em;
}

.margin-top-8 {
  margin-top: 8em;
}

.margin-top-40 {
  margin-top: 40px;
}

.padding-left-0 {
  padding-left: 0em;
}

.padding-top-32 {
  padding-top: 32px;
}

.padding-bottom-0 {
  padding-bottom: 0px;
}

.padding-bottom-48 {
  padding-bottom: 48px;
}

.sign-up-box {
  border: 1px solid #dcdee0;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 22px;
  height: 396px;
  width: 325px;

  + .sign-up-box {
    margin-top: 2em;
    @media screen and (min-width: 921px) {
      margin-right: 3em;
    }
    @media screen and (min-width: 962px) {
      margin-top: 0em;
      margin-left: 40px;
      margin-right: 0em;
    }
  }
}

.large-button.width-100 {
  position: absolute;
  bottom: 20px;
  width: 86%;
}

.tablet-lg-hide {
  display: none;
  @include at-media('tablet-lg') {
    display: block;
  }
}

.tablet-lg-show {
  display: block;
  @include at-media('tablet-lg') {
    display: none;
  }
}

.mobile-margin-top-2 {
  margin-top: 2em;
  @include at-media('tablet') {
    margin-top: 0em;
  }
}

// Side nav fix
.usa-sidenav__sublist {
  .usa-sidenav__item {
    a {
      padding-left: units($sidenav-level-2-inset);
    }
  }

  .usa-sidenav__sublist {
    .usa-sidenav__item {
      a {
        padding-left: units($sidenav-level-3-inset);
      }
    }
  }
}
